*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  margin: 0;
  padding-block: 2rem;
  font-family: sans-serif;
  width: min(90%, 35rem);
  margin-inline: auto;
}

input {
  display: block;
  margin: auto;
}

button {
  border: none;
  outline: none;
}

:root {
  --White: hsl(0, 0%, 100%);
  --Blue: hsl(252, 100%, 63%);
  --Blue-gray: hsl(250, 19%, 40%);
  --Light-gray: hsl(0, 0%, 94%);
  --Black: hsl(0, 0%, 21%);
}

.header {
  background-color: var(--Blue);
}
.header__title {
  color: var(--White);
}
.header__btn {
  color: var(--Blue-gray);
}

.main__title {
  color: var(--Blue-gray);
  font-size: 1.25rem;
}

.header {
  text-align: center;
  padding: 2rem 4rem;
}
.header__input {
  margin-bottom: 1rem;
  padding: 1.25rem;
  max-width: 7.3125rem;
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}
.header__btn {
  padding: 0.56rem 1.72rem;
  border-radius: 5px;
  background-color: var(--White);
}

.main {
  text-align: center;
  background-color: var(--Light-gray);
  padding: 2.25rem 1.5625rem;
}
.main__card {
  background-color: var(--White);
  padding: 1.25rem;
  border-radius: 8px;
}
.main__card + .main__card {
  margin-top: 1.5625rem;
}